@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #121212;
  }
}
.PlayerCard_card__1zQPo {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 0.8rem;
  font-weight: 500;
}

.PlayerCard_card__1zQPo:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.PlayerCard_container__2EhGa {
  padding: 1rem;
}

.PlayerCard_name__2J_rd {
  font-weight: 700;
  color: inherit;
  background-color: none;
}

.PlayerCard_name__2J_rd.PlayerCard_white__14PJh {
  background-color: #121212;
}

.PlayerCard_profilePic__2ZVWr {
  width: 100%;
}

.PlayerCard_negative__1ApMD {
  color: rgba(255, 0, 0);
}

.PlayerCard_score__3ZXsF.PlayerCard_negative__1ApMD::before {
  content: "-$";
}

.PlayerCard_score__3ZXsF::before {
  content: "$";
}

@media (max-width: 768px) {
  .PlayerCard_card__1zQPo {
    width: 100%;
  }

  .PlayerCard_container__2EhGa {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .PlayerCard_profilePic__2ZVWr {
    width: 25%;
  }

  .PlayerCard_updated__2Tcfm {
    display: none;
  }

  .PlayerCard_score__3ZXsF {
      font-size: 1.6rem;
  }
}

@media (prefers-color-scheme: dark) {
  .PlayerCard_card__1zQPo {
    background-color: rgba(255, 255, 255, 12%);
    color: white;
    font-weight: 600;
  }

  .PlayerCard_name__2J_rd.PlayerCard_white__14PJh {
    background-color: inherit;
  }

  .PlayerCard_negative__1ApMD {
    color: rgba(255, 0, 0, 75%);
  }
}

.Scores_container__3LDHH {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  width: 80%;
  margin: 0 auto;
  margin-top: 1rem;
  grid-gap: 1rem;
  gap: 1rem;
}

.Scores_loading__34cey {
  font-size: 3rem;
  font-weight: 700;
}

@media (prefers-color-scheme: dark) {
  .Scores_loading__34cey {
    color: white;
  }
}

.Home_container__3-qjB {
  display: flex;
  justify-content: center;
  width: 80%;
  margin: 0 auto;
}

.Home_container__3-qjB > pre {
    border: 1px solid red;
    /* width: 80%; */
    font-size: 1rem;
    font-weight: 500;
    /* font-family: "Arial Black" */
}

.Home_container__3-qjB > div {
    /* border: 1px solid red; */
    text-align: center;
    font-size: 3rem;
    font-weight: 500;
    font-family: "Audiowide", cursive;
}

@media (prefers-color-scheme: dark) {
  .Home_container__3-qjB > pre {
    color: white;
  }

  .Home_container__3-qjB > div {
    color: white;
  }
}

.Navigation_navbar__2IRSB {
  margin: 10px;
  display: flex;
  justify-content: space-evenly;
}

.Navigation_link__2Svw_ {
  /* padding: 1.5rem; */
  font-size: 2rem;
  font-weight: 700;
}

@media (prefers-color-scheme: dark) {
  .Navigation_link__2Svw_ {
    color: hsl(240, 100%, 75%);
  }

  .Navigation_link__2Svw_:active {
    color: hsl(0, 100%, 75%);
  }

  .Navigation_link__2Svw_:visited {
    color: hsl(271, 68%, 75%);
  }
}

